import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ApiService } from 'src/app/shared/services/api.service';
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import 'rxjs/add/operator/map';
import { DatePipe } from '@angular/common';
import { AccountClosureServiceService } from 'src/app/shared/services/account-closure-service.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { HttpClient } from '@angular/common/http';
import { AppConstants } from 'src/app/shared/config/app.constant';
import * as XLSX from "xlsx";

@Component({
  selector: 'npr-audit-logs',
  templateUrl: './audit-logs.component.html',
  styleUrls: ['./audit-logs.component.scss']
})
export class AuditLogsComponent implements OnInit {
  isLoading:boolean = false;
  isSelected: boolean = true;
  dtOptions: DataTables.Settings = {};
  auditlogdata: any;
  pipe = new DatePipe('en-US');
  roleCodes = new permissionsLabels();
  API_URL = AppConstants.baseURL;
  @ViewChild("format") format: ElementRef;
  totalPages: any;
  totalRecords: any;
  arrayData: any;


  constructor(private api: ApiService,private http: HttpClient, private excelService: AccountClosureServiceService, private cdr:ChangeDetectorRef,private roleService:RoleService) { }

  ngOnInit(): void {
    
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength : 5,
      serverSide: true,
      processing: true,
      columnDefs: [ { type: 'date', 'targets': [5] } ],
      order: [[5, 'desc']],
      // lengthMenu: [[5, 10, 20, 30], [5, 10, 20, 30]],
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
       
      ajax: (dataTablesParameters: any, callback) => {
        ;
        dataTablesParameters.minNumber = dataTablesParameters.start + 1;
        dataTablesParameters.maxNumber =
          dataTablesParameters.start + dataTablesParameters.length;
        console.log(
          dataTablesParameters.minNumber,
          dataTablesParameters.maxNumber
        );
        const pageNo = Math.floor(dataTablesParameters.minNumber / dataTablesParameters.length);
        const pageSize = dataTablesParameters.length;
        
        this.http.get<any>(`${this.API_URL}`+`/auditLogs/getAllAuditLogs?pageNo=${pageNo}&pageSize=${pageSize}`)
          .subscribe(resp => {
            ;
            console.log(resp);
            this.totalPages = resp.totalNoOfPages;
            this.totalRecords = resp.totalRecords;
            this.auditlogdata = resp.result;
            this.isLoading = false;
            this.cdr.markForCheck();

            callback({
              recordsTotal: resp.totalRecords,
              recordsFiltered: resp.totalRecords,
              data: [],
            });
          });
      },
    };

    // this.getAuditLogList();
    this.roleService.fetchScreenPermissions('Audit Logs');
  
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    console.log(this.roleCodes);
  }


  modo(event) {
    console.log(event);
    if (this.format.nativeElement.value == 'PDF') {
     this.downloadLog(this.totalRecords,this.totalPages);
    }
    if (this.format.nativeElement.value === 'Excel') {
      this.exportAsXLSX();
    }

  }
  exportAsXLSX(): void {
    console.log(this.auditlogdata);
    this.http.get<any>(`${this.API_URL}`+`/auditLogs/getAllAuditLogs?pageNo=${0}&pageSize=${this.totalRecords}`)
    .subscribe(resp => {
      this.arrayData=resp.result;
      this.arrayData.forEach(element => {
        element.timeForExport = this.pipe.transform(element.timeForExport, 'dd-MMM-yy');
        element.updatedAt = this.pipe.transform(element.updatedAt, 'dd-MMM-yy');
        element.creatorDtStamp = this.pipe.transform(element.creatorDtStamp, 'dd-MMM-yy');
        element.verifierDtStamp=this.pipe.transform(element.verifierDtStamp,'dd-MMM-yy')
      });
      this.excelService.exportAsExcelFile(this.arrayData, 'Audit_Report');
    });
   
  }

  downloadLog(totalRecords,totalNoOfPages){
    // getSecurityPolicies?pageNo=${0}&pageSize=${1000}&sortBy=${sortBy}`
    this.http.get<any>(`${this.API_URL}`+`/auditLogs/getAllAuditLogs?pageNo=${0}&pageSize=${totalRecords}`)
          .subscribe(resp => {
            this.convert(resp.result)
          });
  }
  convert(data) {

    var doc = new jsPDF();
    var col = [["Screen", "creatorId", "ActionPerformBy", "Record", "Action", "Time"]];
    var rows = [];

  
    var itemNew = data;

    itemNew.forEach(element => {
      var date = new Date(element.creatorDtStamp).toLocaleDateString("en-us");
      console.log(date)
      var temp = [element.log, element.creatorId, element.actionPerformBy, element.record, element.action, date];
      rows.push(temp);
    });

    //doc.autoTable(col, rows);
    doc.autoTable({
      head: col,
      body: rows
    });
    doc.save('AuditLog.pdf');
  }
}
